<template>
  <v-app-bar app dark :clipped-left="true" fixed color="primary">
    <v-app-bar-nav-icon v-on:click="$eventBus.$emit('drawerToggle')" class="ml-2">
      <img src="@/assets/Logo_W.png" width="20px" height="20px" class="logo" alt="">
    </v-app-bar-nav-icon>
    <v-toolbar-title class="text-uppercase ml-2">
      <span class="sub_title">
        {{getTitle}}
      </span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items>
      <template v-for="(item, index) in menu_items">
        <v-btn 
          color="primary"
          class="top_menu"
          depressed
          large 
          :key="index" 
          :to="item.link"
        >{{item.title}}</v-btn>
      </template>
      <v-btn
        icon
        v-on:click=" dialog = true "
        v-if="!getLogin"
        class="ml-3"
      >
        <v-icon>account_circle</v-icon>
      </v-btn>
      <v-btn
        icon
        v-on:click="logout()"
        v-if="getLogin"
        class="ml-3"
      >
        <v-icon>exit_to_app</v-icon>
      </v-btn>
      <!-- login pop_up window -->
      <v-dialog v-model="dialog" persistent max-width="350">        
        <v-card class="pa-3">
          <v-col>
            <v-card-title class="headline">LOGIN</v-card-title>
            <v-col class="pa-4">
              <img :src="require('@/assets/logo.png')" width="160"/>
            </v-col>
            <v-form class="pa-4">
              <v-text-field
                v-model="userId"
                prepend-icon="person"
                data-vv-name="ID"
                :label="$t('basic.lblID')"
                :error-messages="errors.collect('ID')"
                v-validate="'min:2|max:20'"
                type="text"
              ></v-text-field>
              <v-text-field
                v-model="password"
                prepend-icon="account_circle"
                data-vv-name="Password"
                :error-messages="errors.collect('Password')"
                :label="$t('basic.lblPassword')"
                v-validate="'min:2|max:20'"
                type="password"
                v-on:keyup.enter="login()"
              ></v-text-field>
              <!-- <v-select
                v-model="selectLanguage"
                prepend-icon="language"
                :items="language"
                label=" "
              ></v-select> -->
            </v-form>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn class="login_btn" color="secondary" text @click="closePop()">{{$t("basic.btn_cancle")}}</v-btn>
              <v-btn class="login_btn" color="primary" text @click="login()">{{$t("basic.btn_ok")}}</v-btn>
            </v-card-actions>
          </v-col>
        </v-card>
      </v-dialog>
      <!-- login pop_up window -->
    </v-toolbar-items>
  </v-app-bar>
</template>

<script>
import {mapGetters} from 'vuex';
// import $ from "jquery";

export default {
  data: () => ({
    dialog: false,
    language:['en','ko'],
    selectLanguage:'ko',
    userId:'',
    password: '',
    menu_items:[
      {
        title: 'HOME',
        link: '/'
      },
      {
        title: 'CHOK',
        link: '/chok'
      },
      {
        title: 'HISTORY',
        link: '/history'
      },
      {
        title: 'PORTFOLIO',
        link: '/uiux'
      }
    ]
  }),
  computed:{
    ...mapGetters(['getTitle','getLogin'])
  },
  methods: {
    closePop(){
      this.dialog = false;
      this.userId = '';
      this.password = '';
      this.$validator.reset();
    },
    logout(){
      var select = confirm(" ?");
      if(select){
        this.$store.state.isLogin = false;
        this.dialog = false;
        this.$store.commit('login_menu', false);
      }      
    },
    login(){
      if(this.userId !== null || this.userId !== '' || this.password !== null || this.password !== ''){
        var userGroup = this.$store.state.user;
        var userFilter = userGroup.filter((item)=>{
          return item.id == this.userId;
        });
        if(userFilter.length == 0){
          this.$toastr.error( this.$t('basic.ErrorIDMassage'), this.$t('basic.ErrorIDTitle'),{timeOut: 1500});
        }else{
          if(userFilter[0].password == this.password){
            this.$toastr.success( this.$t('basic.SucessLoginMassage'), userFilter[0].id+this.$t('basic.SucessLoginTitle'),{timeOut: 1500});
            this.$store.dispatch('changeLang', true);
            this.$store.state.isLogin = true;
            this.dialog = false;
          }else{
            this.$toastr.error( this.$t('basic.ErrorPWMassage'), this.$t('basic.ErrorPWTitle'),{timeOut: 1500});
          }
        }
      }
    }
  }
}
</script>

<style>
.mainTitle{
  font-size: 1.3rem;
}
.userName{
  font-size: 1.2rem;
}
.sub_title{
  font-size: 1.1rem;
  font-weight: lighter;
}
.login_btn{
  font-weight: 800;
}
@media screen and (max-width: 960px) {
  .top_menu{
    display: none;
  }
}
</style>
