<template>
	<v-container fluid class="col-lg-10">
		<v-row wrap align="start" justify="center">			
			<v-col cols="10">
				<v-card>
					<v-carousel :height="imgHeight" tile hide-delimiter-background show-arrows-on-hover>
						<v-carousel-item 
							v-for="(item,i) in worksItem.contents.img" 
							:key="i"
							:src="require(`@/assets/portfolio/${item}`)" 
							transition="slide"
						></v-carousel-item>
					</v-carousel>
				</v-card>
			</v-col>
			<v-col cols="10" align="start" class="pa-5">
				<v-row>
					<v-col cols="12" md="9">
						<p class="contents_title">{{ worksItem.title }}</p>
						<p class="contents_text">{{ worksItem.contents.text }}</p>
					</v-col>
					<v-col cols="12" md="3" class="contents_date" align-self="end">
						<p>{{ worksItem.contents.ability }}</p>
						<p>{{ worksItem.contents.period }}</p>
					</v-col>
				</v-row>
				<v-row class="pt-3">
					<v-btn 
						:v-if="worksItem.contents.sub" 
						v-for="(sub, index) in worksItem.contents.sub" 
						:key="index"
						:href="sub.to"
						target="_blank"
						class="contents_sub_btn ml-2"
						>
						{{sub.title}}
					</v-btn>
				</v-row>
			</v-col>
			<v-col cols="10">
				<v-sheet class="mx-auto" elevation="1" max-width="100%" color="transparent">
					<v-slide-group v-model="model" center-active show-arrows mandatory>
						<v-slide-item v-for="(item , index) in works" :key="index" v-slot:default="{active, toggle}">
							<v-card class="ma-4" height="130" width="150" v-on:click="selectItem(item,index)"  @click="toggle">
								<v-img :src="require(`@/assets/portfolio/${item.src}`)"></v-img>
								<v-col align="center" justify="center">
									<span class="contents_sub_title">{{item.title}}</span>
								</v-col>
							</v-card>
						</v-slide-item>
					</v-slide-group>
				</v-sheet>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
import $ from "jquery";
export default {
	data: () => ({
		model:0,
		imgHeight: '100%',
		worksItem: []
	}),
	computed:{
		works(){
			return this.$store.getters.getWorks;
		}
	},
	created() {
		this.worksItem = this.$store.getters.getWorksItem;
		window.addEventListener('resize', this.handleResize)
		this.handleResize();
  },
	mounted(){
		this.$store.dispatch('change_title',this.worksItem.title);
	},
	methods:{
		handleResize(){
			var width = window.innerWidth;
			// console.log("width",width);
			// if( width < 600 ){
			// 	this.imgHeight = 400;
			// }else{
			// 	this.imgHeight = 650;
			// }
		},
		selectItem(item, index){
			this.worksItem = item;
			this.$store.dispatch('change_title',this.worksItem.title);
		}
	},
	watch:{
	},
	updated() {
		this.$nextTick(function () {
		});
	},
	destroyed() {
		window.removeEventListener('resize', this.handleResize)
	}
}
</script>

<style>
.contents_title{
	text-align: left;
	font-size: 1.5rem;
	font-weight: 600;
	color: #143b83;
}
.contents_text{
	font-size: 1.1rem;
	color: #414141;
}
.contents_date{
	font-size: 0.9rem;
	font-weight: lighter;
	color: #414141;
	height: 100%;
	text-align: right;
}
.contents_sub_title{
	vertical-align: center;
	font-size: 0.9rem;
	font-weight: 700;
	color: #192E49;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1; /*  */
	-webkit-box-orient: vertical;
	word-break: keep-all;
	word-wrap:break-word; 
}
.contents_sub_btn{
	font-size: 1rem !important;
}
@media screen and (max-width: 768px) {
	.contents_title{
		font-size: 1.2rem;
	}
	.contents_text{
		font-size: 0.9rem;
	}
	.contents_date{
		font-size: 0.75rem;
	}
	.contents_sub_btn{
		font-size: 0.75rem !important;
	}
}
</style>