<template>
  <v-app-bar app dark :clipped-left="true" fixed color="primary">
    <v-row xs="12">
      <v-row xs="6" class="ml-3" align="center" justify="start">
        <v-app-bar-nav-icon v-on:click="$eventBus.$emit('drawerToggle')">
          <img src="@/assets/Logo_W.png" width="20px" height="20px" class="logo" alt="">
        </v-app-bar-nav-icon>
        <v-toolbar-title class="text-uppercase ml-2">
          <span class="sub_title">
            {{getTitle}}
          </span>
        </v-toolbar-title>
      </v-row>
      <v-row xs="6" justify="end" align="center">
        <v-row xs="8" md="11">
          <v-tabs height="64" right class="menu" background-color="primary">
            <v-tabs-slider></v-tabs-slider>
            <v-tab 
              v-for="item in menu_items"
              :key="item.title"
              :to="item.link"
            >{{item.title}}</v-tab>
          </v-tabs>
        </v-row>
        <v-col xs="4" sm="2" md="1" xl="1" class="ml-2 mr-3">
          <v-btn
            icon
            v-on:click="login()"
            v-if="!getLogin"
          >
            <v-icon>account_circle</v-icon>
          </v-btn>
          <v-btn
            icon
            v-on:click="logout()"
            v-if="getLogin"
          >
            <v-icon>exit_to_app</v-icon>
          </v-btn>
          <!-- login pop_up window -->
          <v-dialog v-model="dialog" persistent max-width="300">        
            <v-card>
              <v-col>
                <v-card-title class="headline">LOGIN</v-card-title>
                <v-col class="pa-4">
                  <img :src="require('@/assets/logo.png')" width="160"/>
                </v-col>
                <v-form class="pa-4">
                  <v-text-field
                    v-model="userId" 
                    prepend-icon="person"
                    label="ID" 
                    type="text"
                  ></v-text-field>
                  <v-text-field
                    v-model="password" 
                    prepend-icon="lock"
                    label="Password"
                    type="password"
                    v-on:keyup.enter="login()"
                  ></v-text-field>
                  <v-select
                    v-model="selectLanguage"
                    prepend-icon="language"
                    :items="language"
                    label=" "
                  ></v-select>
                </v-form>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="secondary" text @click="dialog = false">{{$t("basic.btn_cancle")}}</v-btn>
                  <v-btn color="primary" text @click="loginCheck()">{{$t("basic.btn_ok")}}</v-btn>
                </v-card-actions>
              </v-col>
            </v-card>
          </v-dialog>
          <!-- login pop_up window -->
        </v-col>
      </v-row>
    </v-row>
  </v-app-bar>
</template>

<script>
import {mapGetters} from 'vuex';
// import $ from "jquery";

export default {
  data: () => ({
    dialog: false,
    language:['en','ko'],
    selectLanguage:'ko',
    userId:'',
    password: '',
    menu_items:[
      {
        title: 'PORTFOLIO',
        link: '/home'
      },
      {
        title: 'CHOK',
        link: '/chok'
      },
      {
        title: 'HISTORY',
        link: '/history'
      }
    ]
  }),
  computed:{
    ...mapGetters(['getTitle','getLogin'])
  },
  methods: {
    logout(){
      var select = confirm(" ?");
      if(select){
        this.$store.state.isLogin = false;
        this.dialog = false;
        this.$store.commit('login_menu', false);
      }      
    },
    login(){
      this.dialog = true;
    },
    loginCheck(){
      this.$store.commit('login_menu', true);
      this.$store.state.isLogin = true;
    }
  }
}
</script>

<style>
.mainTitle{
  font-size: 1.3rem;
}
.userName{
  font-size: 1.2rem;
}
.sub_title{
  font-size: 1.1rem;
  font-weight: lighter;
}
@media screen and (max-width: 960px) {
  .menu{
    display: none;
  }
}
</style>
