                      </v-flex>
                      <v-flex>
                        <v-dialog
                          v-model="passwordFindDialog"
                          max-width="450"
                        >
                          <v-card>
                            <v-layout fluid column fill-height pa-5>
                              <v-card-title class="headline mb-5">
                                 
                              </v-card-title>
                              <v-card-text>
                                 ID Email .
                              </v-card-text>
                              <v-flex ml-3 mr-3>
                                <v-text-field
                                  v-model="findUserId" 
                                  prepend-icon="person"
                                  label="ID" 
                                  type="text"
                                  data-vv-name="Id"
                                  :error-messages="errors.collect('Id')"
                                  v-validate="'required|min:2|max:20'"
                                ></v-text-field>
                              </v-flex>
                              <v-flex ml-3 mr-3 mb-4>
                                <v-text-field
                                  v-model="findUserEmail" 
                                  prepend-icon="email"
