                                </v-btn>
                              </v-card-actions>
                            </v-layout>
                          </v-card>
                        </v-dialog>
                      </v-flex>
                      <v-flex>
                        <v-dialog
                          v-model="userAddFindDialog"
                          max-width="650"
                        >
                          <v-card>
                            <v-layout fluid column fill-height pa-5>
                              <v-card-title class="headline mb-3">
                                 
                              </v-card-title>
                              <v-flex>
                                <v-layout row ml-3 mr-3>
                                  <v-text-field
                                    v-model="addUserId" 
                                    prepend-icon="person"
                                    label="ID" 
                                    type="text"
