                                    :error-messages="errors.collect('Id')"
                                    v-validate="'required|min:2|max:20'"
                                  ></v-text-field>
                                  <v-btn color="secondary ml-3" large>
                                    ID 
                                  </v-btn>
                                </v-layout>
                              </v-flex>
                              <v-flex>
                                <v-layout ml-3 mr-3>
                                  <v-text-field
                                    v-model="addUserPassword" 
                                    prepend-icon="lock"
                                    label="" 
                                    type="password"
                                    data-vv-name="Password"
                                    :error-messages="errors.collect('Password')"
                                    v-validate="'required|min:6'"                              
                                    ref="password"
                                  ></v-text-field>
                                </v-layout>
                              </v-flex>
                              <v-flex>
                                <v-layout ml-3 mr-3>
                                  <v-text-field
                                    v-model="addUserPasswordConfirm" 
                                    prepend-icon="done"
                                    label="" 
                                    type="password"
                                    data-vv-name="Password_confirmation"
                                    :error-messages="errors.collect('Password_confirmation')"
                                    v-validate="'required|confirmed:password'"
                                  ></v-text-field>
                                </v-layout>
                              </v-flex>
                              <v-flex>
                                <v-layout row ml-3 mr-3>
                                  <v-flex>
                                    <v-text-field
                                      v-model="addUserName" 
                                      prepend-icon="account_circle"
                                      label="" 
