<script>
  export default {
    data: () => ({
      loginTitle: 'Teachers In Korea  ',
      idFindDialog: false,
      passwordFindDialog: false,
      userAddFindDialog: false,
      findUserName: null,
      findUserId: null,
      findUserEmail: null,
      addUserId: null,
      addUserPassword: null,
      addUserPasswordConfirm: null,
      addUserEmail: null,
      addUserName: null,
      addUserMiddleName: null,
      addUserFamilyName: null,
      addUserPhone: null,
      addUserOfficeNumber: null,
      addUserPostalNumber: null,
      addUserAddress: null,
      addUserAddressDetail: null,
      addUserLanguage: null,
      language:['English',''],
      selectLanguage:'',
      userId:'mis007',
      password: '12345'
    }),
    methods:{
      login(){
        if(this.userId === this.$store.state.userId && this.password === this.$store.state.password){
          // this.$router.push('/');
          this.$store.state.accessToken = true;
        }if(this.userId !== this.$store.state.userId){
          this.$toastr.error( ' ID  .', ' ID ',{timeOut: 1500});
        }if(this.password !== this.$store.state.password){
          this.$toastr.error( '   .', ' ',{timeOut: 1500});
        }
      }
    }
  }
</script>

<style>
.login_btn{
  padding: 0 6rem;
}
.login_sub_btn{
  font-weight: 600;
  color: #1e3765 !important;
}
</style>
