import Vue  from 'vue';
import Vuex from 'vuex';

var i18n = require('./i18n').default;
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isLogin: false,
    user:[
      {
        id: 'chok0908',
        password: 'wjrxhak98'
      },
      {
        id: 'test',
        password: '123456'
      }
    ],
    menus: i18n.t("menu"),
    works:[],
    uiux: i18n.t("uiux"),
    design: i18n.t("design"),
    plan: i18n.t("plan"),
    personal: i18n.t("personal"),
    ui: {
      subject:'uiux',
      title:'',
      worksItem:[]
    }
  },
  actions: {
    // stats    actions   mutations commit.
    changeLang({commit},data){
      commit('changeLang',data);
      commit('changeWorks');
    },
    change_title({commit},data){
      commit('change_title',data);
    },
    change_subject({commit},data){
      commit('change_subject',data);
    },
    changeWorks({commit}){
      commit('changeWorks');
    }
  },
  getters: {
    getTitle: function(state){
      return state.ui.title;
    },
    getSubject: function(state){
      return state.ui.subject;
    },
    getLogin: function(state){
      return state.isLogin;
    },
    getMenus: function(state){
      return state.menus;
    },
    getUIUX: function(state){
      return state.uiux;
    },
    getDesign: function(state){
      return state.design;
    },
    getPlan: function(state){
      return state.plan;
    },
    getWorks: function(state){
      return state.works;
    },
    getWorksItem: function(state){
      return state.ui.worksItem;
    }
  },
  mutations: {
    change_title(state, data){
      state.ui.title = data;
    },
    change_subject(state, data){
      state.ui.subject = data;
    },
    changeLang(state, data){
      if(data == true){
        state.menus = i18n.t("menu_login");
      }else{
        state.menus = i18n.t("menu");
      }
    },
    changeWorks(state){
      if(state.ui.subject == 'uiux')      { state.uiux    = i18n.t("uiux");       state.works = state.uiux;}
      if(state.ui.subject == 'design')    { state.design  = i18n.t("design");     state.works = state.design;}
      if(state.ui.subject == 'plan')      { state.plan    = i18n.t("plan");       state.works = state.plan;}
      if(state.ui.subject == 'personal')  { state.personal= i18n.t("personal");   state.works = state.personal;}
    },
    push_worksItem(state, data){
      state.ui.worksItem = data;
    }
  }
});