<template>
	<v-container fluid>
		<v-col class="container col-lg-11 col-md-12 pt-3 homeContents">
			<v-row align="center">
				<h3 class="subject_title">PORTFOLIO <span class="subject_sub_title">| {{$t("basic.portfolio_subtitle")}}</span></h3>
				<v-spacer></v-spacer>
				<v-col xs="10" md="3" lg="3" class="searchBar">
					<v-text-field
						v-model="searchKeyword"
						hide-details
						append-icon="search"
						flat
						color="primary"
					></v-text-field>
				</v-col>
			</v-row>
			<v-item-group>
				<v-container fluid grid-list-sm>
					<v-row wrap>
						<template v-for="(works_items, index) in worksItems">
							<v-col cols="6" md="6" lg="4" :key="index">
								<v-item>
									<v-card class="d-flex align-center portfolio_item">
										<v-col class="pa-0">
											<v-row class="img_box">
												<v-btn class="portfolio_btn pa-0" v-on:click="workSelect(works_items)">
													<v-img :src="require(`@/assets/portfolio/${works_items.src}`)" class="portfolio_img" aspect-ratio="1.4">
													</v-img>
												</v-btn>
											</v-row>
											<v-card-title class="portfolio_text_group mb-2" v-on:click="workSelect(works_items)">
												<h3 class="portfolio_title">{{works_items.title}}</h3>
												<span class="portfolio_text">{{works_items.text}}</span>
											</v-card-title>
										</v-col>
									</v-card>
								</v-item>
							</v-col>
						</template>
					</v-row>
				</v-container>
			</v-item-group>
		</v-col>
	</v-container>
</template>

<script>
export default {
	props:[''],
    data: () => ({
		searchKeyword: ''
	}),
	computed:{
		worksItems(){
			return this.$store.getters.getWorks.filter(data => {
                return data.title.toLowerCase().includes(this.searchKeyword.toLowerCase());
            });
		}
	},
	mounted(){
	},
	methods:{
		workSelect(value){
			value.active = true;
			this.$store.commit('push_worksItem',value);
			this.$router.push('/contents').catch(err => err);
		}
	},
	watch:{
	},
	updated(){
		
	}
}
</script>

<style>
.subject_title{
	padding: 3.5rem 4rem;
	text-align: left;
	font-size: 2rem;
	font-weight: 600;
	color: #143b83;
}
.subject_sub_title{
	font-size: 1rem;
	padding-left: 1rem;
	font-weight: 300;
	color: #565656;
}
.portfolio_item{
	border-radius: 1rem !important;
	margin-left: 1.6rem !important;
	margin-right: 1.6rem !important;
	margin-bottom: 3.5rem !important;
	color: #2653A3 !important;
	overflow: hidden !important;
}
.portfolio_item:hover{
	background-color: #f8f8f8 !important;
	color: #143b83 !important;
	text-decoration: none;
}
.portfolio_img:hover{
	transform: scale(1.1);
	-webkit-transform: scale(1.1);
	-moz-transform: scale(1.1);
	-ms-transform: scale(1.1);
	-o-transform: scale(1.1);
}
.img_box{
	overflow: hidden !important;
}
.portfolio_img{
	border-radius: 1rem 1rem 0 0;
	height: 300px;
	-webkit-transform:scale(1);
	-moz-transform:scale(1);
	-ms-transform:scale(1);
	-o-transform:scale(1);
	transform:scale(1);
	-webkit-transition:.5s;
	-moz-transition:.5s;
	-ms-transition:.5s;
	-o-transition:.5s;
	transition:.5s;
}
.portfolio_title{
	font-size: 1.1rem;
	text-align: left;
	font-weight: 600;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1; /*  */
	-webkit-box-orient: vertical;
	word-wrap:break-word;
	padding-left: 0.1rem;
	padding-bottom: 0.2rem;
	width:100%;
}
.portfolio_text{
	font-size: 0.9rem;
	text-align: left;
	font-weight: 500;
	color: #6d6d6d;
	float: left;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2; /*  */
	-webkit-box-orient: vertical;
	word-break: keep-all;
	word-wrap:break-word; 
	line-height: 1.3em;
	height: 2.5em;
	width:100%;
}
.portfolio_text_sub{
	font-size: 1.1rem;
	color: #143b83;
}
.portfolio_img:hover > .portfolio_title{
	color: #fff;
}
.portfolio_text_group{
	padding: 1.2rem;
}
.v-card--reveal {
	align-items: center;
	bottom: 0;
	justify-content: center;
	opacity: .5;
	position: absolute;
	width: 100%;
}
/*-----------------------------------------common portfolio style--------------*/
.disabled{
	color: #6b6b6b;
	pointer-events: none;
}
.portfolio_btn{
	width: 100% !important;
	height: 100% !important;
}
.searchBar{
	margin-right: 2.5rem;
	display: block;
}
@media screen and (max-width: 1200px) {
	.subject_title{
		padding: 3.5rem 5rem;
		text-align: left;
	}
	.subject_sub_title{
		padding-left: 1rem;
	}
}
@media screen and (max-width: 990px) {
	.subject_title{
		width: 100%;
		margin: 0;
		font-size: 1.5rem;
		font-weight: 550;
		padding: 1.5rem 1rem;
	}
	.subject_sub_title{
		font-size: 0.9rem;
		padding-left: 0.5rem;
	}
	.portfolio_item{
		margin-right: 1rem !important;
		margin-bottom: 1rem !important;
	}
	.portfolio_item{
		margin-left: 0rem !important;
		margin-bottom: 2rem !important;
		height: 300px;
	}
	.portfolio_img{
		height: 200px;
	}
	.portfolio_title{
		font-size: 1.3rem;
	}
	.portfolio_text{
		font-size: 0.9rem;
	}
	.portfolio_text_sub{
		font-size: 0.8rem;
	}
	.portfolio_text_group{
		padding: 1.1rem;
	}
	.searchBar{
		display: none;
	}
}
@media screen and (max-width: 960px) {
	
	.portfolio_item{
		margin-right: 0rem !important;
	}
	.portfolio_title{
		font-size: 1rem;
	}
	.portfolio_sub_title{
		margin-left: 0;
		font-size: 0.7rem;
	}
	.portfolio_img{
		height: 180px;
	}
}
</style>
