<template>
  <v-navigation-drawer
    v-model="drawer"
    fixed
    clipped
    app
    width="230"
  >
    <v-col align="center">
      <v-flex>
        <a href="/">
          <img class="logo_img" :src="require('@/assets/logo.png')"/>
        </a>
      </v-flex>
    </v-col>
    <v-divider></v-divider>
    <v-list dense expand rounded>
      <v-list-group
        v-for="items in menuItems"
        :key="items.title"
        v-model="items.active"
      > 
        <template v-slot:activator>
          <v-list-item-content>
            <v-list-item-title v-text="items.title"></v-list-item-title>
          </v-list-item-content>
        </template>

        <v-list-item
          v-for="subItem in items.subItems"
          :key="subItem.title"
          :to="subItem.to"
        >
          <v-list-item-icon>
            <v-icon v-text="subItem.icon"></v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="subItem.title"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-group>
    </v-list>
    <v-divider></v-divider>
    <v-col class="d-flex" cols="12">
      <span class="lang_s">{{$t("basic.app_lang")}}</span>
      <v-select 
        dense
        v-model="$i18n.locale"
        :items="langs"
        v-bind="languageUpdate($i18n.locale)"
        outlined
      >
      </v-select>
    </v-col>
  </v-navigation-drawer>
</template>

<script>
import ko_m from 'vee-validate/dist/locale/ko.js';
import en_m from 'vee-validate/dist/locale/en.js';
import ko_validate from '@/lang/ko/validate.json';
import en_validate from '@/lang/en/validate.json';
export default {
  data: () => ({
    drawer: true,
    langs: ['ko', 'en'],
    dictionary:{
      ko: {
        attributes: ko_validate,
        messages: ko_m.messages
      },
      en: {
        attributes: en_validate,
        messages: en_m.messages
      }
    }
  }),
  created() {
    this.$eventBus.$on('drawerToggle',this.drawerToggle);
    // this.$eventBus.$on('isLogin', () =>{ this.isLogin() });
  },
  mounted() {
  },
  computed: {
    menuItems(){
      return this.$store.state.menus;
    }
  },
  methods: {
    drawerToggle(){
      this.drawer = !this.drawer;
      //window.console.log('drawer',this.drawer);
    },
    languageUpdate(value){
      // console.log(this.$root.$i18n);
      this.$store.dispatch('changeLang',this.$store.state.isLogin);
      // veevalidation langs change
      if(value == 'en'){
        this.$validator.localize('en', this.dictionary.en);
      }if(value == 'ko'){
        this.$validator.localize('ko', this.dictionary.ko);        
      }
    }
  },
  watch:{
    menuItems(value){
      console.log("value",value)
      if(this.$i18n.locale == 'en'){
        this.$store.state.uiux = this.$i18n.messages.en.uiux;
        this.$store.state.design = this.$i18n.messages.en.design;
        this.$store.state.plan = this.$i18n.messages.en.plan;
      }
      this.$store.dispatch('changeLang',this.$store.state.isLogin);
    }
  }
}
</script>

<style>
.logo_img{
  width: 100%;
  padding: 3rem;
}
.menu{
  width: 100%;
}
.lang_s{
  padding: 0.5rem 1rem;
  font-size: 0.85rem;
}
.v-list-item--active{
  color: #1867c0 !important;
  font-weight: 600 !important;
}
.v-list--dense .v-list-group--no-action>.v-list-group__items>div>.v-list-item {
  padding-left: 40px;
}
.v-list-item__content{
  text-align: left;
}
.v-list-group__header{
  padding: 0 25px;
}
.drawer{
  border: 1px solid #ededed;
}
.v-list--dense .v-list-item .v-list-item__subtitle, .v-list--dense .v-list-item .v-list-item__title, .v-list-item--dense .v-list-item__subtitle, .v-list-item--dense .v-list-item__title {
    font-size: .8125rem;
    font-weight: 700;
    line-height: 1rem;
}
</style>
