import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  base: __dirname,
  mode: 'history',
  scrollBehavior: function(to) {
	if (to.history) {
		return { selector: to.history }
			} else {
			return { x: 0, y: 0 }
		}
	},
  routes: [
    {
      path: '/',
      name: 'home',
      component: ()=> import('@/page/00_user/home')
    },
    {
      path: '/chok',
      name: 'chok',
      component: ()=> import('@/page/00_user/chok')
    },
    {
      path: '/history',
      name: 'history',
      component: ()=> import('@/page/00_user/history')
    },
    {
      path: "/uiux",
      name: "uiux",
      component: ()=> import('@/page/01_portfolio/uiux')
    },
    {
      path: "/design",
      name: "design",
      component: ()=> import('@/page/01_portfolio/design')
    },
    {
      path: "/plan",
      name: "plan",
      component: ()=> import('@/page/01_portfolio/plan')
    },
    {
      path: "/personal",
      name: "personal",
      component: ()=> import('@/page/02_art/personal')
    },
    {
      path: "/contents",
      name: "contents",
      component: ()=> import('@/components/contents/contents')
    },
    {
      path: '/test1',
      name: 'test1',
      component: ()=> import('@/page/03_test/test1'),
      children:[
        {
          path: '/t_1',
          name: 't_1',
          component: ()=> import('@/components/test/home')
        },
        {
          path: '/t_2',
          name: 't_2',
          component: ()=> import('@/components/test/forms')
        },
        {
          path: '/t_3',
          name: 't_3',
          component: ()=> import('@/components/test/animations')
        }
      ]
    },
    {
      path: '/test2',
      name: 'test2',
      component: ()=> import('@/page/03_test/test2')
    }
  ]
});